
.page-enter-active, .page-leave-active {
  -webkit-transition: .3s;
  transition: .3s;
}
.page-enter, .page-leave-to {
  opacity: 0;
}
html, body {
  margin: 0;
  width: 100%;
  min-height: 100vh;
  font-family: 'Times New Roman', serif;
  letter-spacing: 1px;
}
h1, .title1 {
  font-weight: bold;
  font-size: 22px;
  letter-spacing: 1.2px;
  margin: 10px 0px;
}
h2, .title2 {
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 1.2px;
  margin: 10px 0px;
}
p, .p {
  font-size: 18px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1.6;
  margin: 0;
}
.poem {
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1.6;
}
.poem_title {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: 1.2px;
  margin: 10px 0px;
}
a {
  text-decoration: none;
  color: black;
}
.content a {
  padding: 3px;
  font-weight: 400;
  text-decoration: none;
  border: 1px dotted #777;
  border-radius: 5px;
  background-color: #f7f7f7;
  cursor: pointer;
  color: black;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.content a:hover {
    color: #cc2926;
    background-color: #f7f7f7;
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
            box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
}
button {
  cursor: pointer;
  margin: 18px 18px 0px 0px;
  background-color: transparent;
  padding: 8px;
  border: 1px dotted #777;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  border-radius: 5px;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
}
button:hover {
    border: 1px solid #777;
    background-color: #f7f7f7;
    -webkit-transform: translate(-1px, -1px);
            transform: translate(-1px, -1px);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
            box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
}
#app .header {
  height: 160px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1);
}
#app .header .list ul {
    float: right;
    list-style: none;
    margin-top: 85px;
    margin-right: 80px;
    padding: 0;
}
#app .header .list ul li {
      margin: 0px 15px;
      padding: 5px 20px;
      font-size: 20px;
      display: inline-block;
      cursor: pointer;
      -webkit-transition: 0.2s;
      transition: 0.2s;
}
#app .header .list ul li:hover {
        background-color: #f7f7f7;
        -webkit-transform: translate(-1px, -1px);
                transform: translate(-1px, -1px);
        -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
                box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
}
#app .header .logo {
    left: 30px;
    top: 40px;
    padding: 10px 20px;
    position: relative;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    width: 240px;
    height: 90px;
}
#app .header .logo:hover {
      background-color: #f7f7f7;
      -webkit-transform: translate(-1px, -1px);
              transform: translate(-1px, -1px);
      -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
              box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
}
#app .header .logo .up {
      text-align: left;
      font-size: 24px;
      letter-spacing: 3px;
      font-weight: 300;
      border-bottom: 1px solid #000;
      padding: 3px;
}
#app .header .logo .down {
      margin-top: 4px;
      text-align: right;
      font-size: 21px;
      letter-spacing: 1px;
      font-weight: 100;
}
#app .content {
  margin-top: 10px;
  margin-left: 100px;
  margin-right: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#app .content .left {
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 60px;
    width: 100%;
    height: auto;
    min-height: 40vh;
}
#app .content .right {
    margin-top: 20px;
    width: 280px;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
#app .content .right .layout {
      width: 260px;
      max-height: 260px;
      margin: 15px;
      -webkit-transition: 0.2s;
      transition: 0.2s;
      overflow: hidden;
}
#app .content .right .layout img {
        width: 100%;
        height: 100%;
}
#app .content .right .layout:hover {
        -webkit-transform: translate(-2px, -2px);
                transform: translate(-2px, -2px);
        -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
                box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
}




















































































































